.list {
  font-size: 18px;
  color: rgb(50, 55, 62);
}

.list-item {
  /* list-style: none; */
  margin: 20px;
}

.menu {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  height: 100vh;
  width: 480px;
  background-color: #f2f3f5;
  position: absolute;
  z-index: 3;
  box-shadow: rgba(0, 0, 0, 40%) 0px 20px 40px;
  overflow: hidden;
}

.menu-close {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  width: 0px;
}

.menu-wrapper {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  height: 100vh;
  margin-top: 100px;
  width: 480px;
  background-color: #f2f3f5;
  position: absolute;
  z-index: 3;
}

.menu-wrapper-close {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  width: 480;
  opacity: 0;
}

.link {
  font-size: 24px;
  margin-left: 10px;

  /* position: absolute; */
}

.icon-btn {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  z-index: 4;
  cursor: pointer;
  top: 20px;
  left: 30px;
  width: 40px;
  height: 40px;
  border-radius: 20px;
}

.icon-left {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  height: 2px;
  width: 10px;
  top: 19px;
  left: 10px;
  background-color: rgb(50, 55, 62);
  border-radius: 1px 0 0 1px;
}

.icon-right {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  height: 2px;
  width: 10px;
  top: 19px;
  left: 20px;
  background-color: rgb(50, 55, 62);
  border-radius: 0 1px 1px 0;
}

.icon-left:before {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  height: 2px;
  width: 10px;
  background-color: rgb(50, 55, 62);
  content: "";
  top: -7.5px;
  border-radius: 1px 0 0 1px;
}

.icon-left:after {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  height: 2px;
  width: 10px;
  background-color: rgb(50, 55, 62);
  content: "";
  top: 7.5px;
  border-radius: 1px 0 0 1px;
}

.icon-right:before {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  height: 2px;
  width: 10px;
  background-color: rgb(50, 55, 62);
  content: "";
  top: -7.5px;
  border-radius: 0 1px 1px 0;
}

.icon-right:after {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  height: 2px;
  width: 10px;
  background-color: rgb(50, 55, 62);
  content: "";
  top: 7.5px;
  border-radius: 0 1px 1px 0;
}

.icon-left-open {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  background: transparent;
}

.icon-right-open {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  background: transparent;
}

.icon-left-open:before {
  -webkit-transform: rotateZ(45deg) scaleX(1.5) translate(1px, 2px);
          transform: rotateZ(45deg) scaleX(1.5) translate(1px, 2px);
}

.icon-left-open:after {
  -webkit-transform: rotateZ(-45deg) scaleX(1.5) translate(1px, -2px);
          transform: rotateZ(-45deg) scaleX(1.5) translate(1px, -2px);
}

.icon-right-open:before {
  -webkit-transform: rotateZ(-45deg) scaleX(1.5) translate(-1px, 2px);
          transform: rotateZ(-45deg) scaleX(1.5) translate(-1px, 2px);
}

.icon-right-open:after {
  -webkit-transform: rotateZ(45deg) scaleX(1.5) translate(-1px, -2px);
          transform: rotateZ(45deg) scaleX(1.5) translate(-1px, -2px);
}
