  text-align: right;
  font-size: 24px;
  padding: 20px 24px;
  margin-right: 8px;
  margin-top: 8px;
  text-align: right;
  float: left;
  border-radius: 16px;
  background: -webkit-linear-gradient(left, #00C4FF, #6C00FF);
  background: linear-gradient(90deg, #00C4FF, #6C00FF);
  cursor: pointer;
}

.b3_button:hover,
.b3_operate:hover,
.b3_delete:hover,
